$shadow-color: rgba(0, 0, 0, 0.24);
$shadow: $shadow-color 1px 1px 2px 1px;
$backgroundColor: white;

// Main colors
$color-important: #7da1c4;
$color-focus: #5e7c99;
$color-standard: #eaeaea;
$color-inactive: #d2d2d2;
$color-toggle-on: #e87722;
$color-toggle-focus: #a06439;
$color-toggle-off: #f7d3b8;
$color-toggle-inactive: #d2d2d2;
$color-zebra-grey: #f7f7f7;
$dark: #2f3940;
$white: white;

$gray-dark: #4c4c4e;
$gray: #98989b;
$gray-light: #e7e7dd;

// Borders
$btn-border: 1px solid;
$btn-border-radius: 4px;

// Font families
$font-special-normal: 'Open Sans',
sans-serif;
$font-default: 'Open Sans',
sans-serif;

// Font sizes
$font-xsmall: 14px;
$font-small: 16px;
$font-medium: 17px;
$font-large: 24px;
$font-xlarge: 36px;

// Line heights
$line-height-xsmall: 1.3;
$line-height-small: 1.5;
$line-height-medium: 22px;
$line-height-large: 31px;
$line-height-xlarge: 40px;



