  }
}

/*
  Defines a class which disables click on the
  element but allows click on all child elements.
*/
.clickThrough {
  pointer-events: none;

  >* {
    pointer-events: all;
  }
}

.transitionBtn {
  transition: color 0.15s linear;
  /* vendorless fallback */
  -o-transition: color 0.15s linear;
  /* opera */
  -ms-transition: color 0.15s linear;
  /* IE 10 */
  -moz-transition: color 0.15s linear;
  /* Firefox */
  -webkit-transition: color 0.15s linear;
  /*safari and chrome */
}



