/*Tooltip start*/
.tooltip {
  position: relative;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  color: white;
  padding: 4px 8px;
  opacity: 0.7;
  white-space: nowrap;
}

.tooltip-measure {
  opacity: 1;
  font-weight: bold;
}

.tooltip-static {
  background-color: #ffcc33;
  color: black;
  border: 1px solid white;
}

.tooltip-measure:before,
.tooltip-static:before {
  border-top: 6px solid rgba(0, 0, 0, 0.5);
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  content: "";
  position: absolute;
  bottom: -6px;
  margin-left: -7px;
  left: 50%;
}

.tooltip-static:before {
  border-top-color: #ffcc33;
}

.tooltip-info {
  opacity: 1;
}

.tooltip-info:before {
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid rgba(0, 0, 0, 0.5);
  content: "";
  position: absolute;
  margin-left: -6px;
  left: 50%;
  top: -6px;
}

.tooltip-absolute {
  left: 50%;
  pointer-events: none;
  position: absolute;
  top: calc(100% + 6px);
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

.has-tooltip .tooltip {
  display: none;
}

.has-tooltip:hover .tooltip {
  display: block;
}

/*Tooltip end*/

