  font-size: 15px;
  font-family: $font-default;
  text-align: left;
}

.dropdown-menu {
  top: inherit;
  min-width: 100px;
  width: 100%;
}

.dropdown-menu-relative {
  position: relative;
  max-height: 150px;
  overflow-x: auto;
}

.dropdown-menu>li>span {
  display: block;
  padding: 1px 20px;
  clear: both;
  font-weight: 400;
  //line-height: 1.42857143;
  color: #333;
  white-space: nowrap;
}



.search-options {
  display: inline-block;
  width: 37px;
  text-align: center;
}

.body-overlay {
  width: 100%;
  height: 100%;
  z-index: 15;
  position: absolute;
  @extend .clickThrough;
}

.legend-panel {
  margin-left: 10px;
}

.logo-panel {
  text-align: center;
  padding-top: 20px;

  p {
    margin-top: 6px;
    margin-bottom: 2px;
    line-height: 1;
    font-size: 15px;
  }
}

.norgeskart-logo {
  margin-left: 10px;
  margin-right: 10px;
  padding: 12px 45px 15px 5px;

  h1 {
    font-size: 22px;
    margin: 0;

    a {
      color: grey !important;
    }
  }

  button {
    color: $color-inactive;
    display: block;
    height: 36px;
    opacity: 1.0;
    position: relative;
    right: -30px;
    width: 30px;

    .glyphicon {
      padding-left: 0;
    }
  }
}

.no-gutter>[class*='col-'] {
  padding-right: 0;
  padding-left: 0;
}

.display-inline {
  display: inline;
}

.full-width {
  width: 100%;
}

.husnum {
  padding-left: 15px;
  margin-top: 4px;

  li {
    border-bottom: none;
    padding: 6px 0 5px 6px !important
  }

  li:hover {
    background-color: lightgray !important;
  }
}

.logo-overlay {
  z-index: 12;
  position: absolute;
  bottom: 0px;
  left: 19px;
  width: 105px;
}

.change-base-map-overlay {
  z-index: 16;
  position: absolute;
  bottom: 25px;
  right: 19px;
  width: 115px;
  box-shadow: rgba(0, 0, 0, 0.24) 1px 1px 3px 1px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
}

.change-base-layer-overlay {
  z-index: 16;
  position: absolute;
  bottom: 25px;
  right: 19px;
  min-width: 100px;
  box-shadow: rgba(0, 0, 0, 0.24) 1px 1px 3px 1px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  background-color: white;

  .btn {
    padding: 3px;
  }
}



.tab-content {
  min-height: 100px;
  border-right: 1px solid $color-toggle-on;
  border-left: 1px solid $color-toggle-on;
  border-bottom: 1px solid $color-toggle-on;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  padding: 10px;
}

.nav-tabs {
  border-bottom: 1px solid $color-toggle-on;
}

.navTabsTransparent {
  border-bottom: 1px solid transparent;

  li>a {
    border-color: $color-toggle-off !important;
    border-radius: 4px !important;
  }

  li>a:hover {
    border-color: $color-toggle-focus !important;
  }

  li.active>a,
  li.active>a:focus,
  li.active>a:hover {
    background-color: $color-toggle-on !important;
    border: 1px solid $color-toggle-on !important;
  }
}

.nav-tabs>li.active>a,
.nav-tabs>li.active>a:focus,
.nav-tabs>li.active>a:hover {
  color: white;
  cursor: default;
  background-color: $color-toggle-on;
  border: 1px solid $color-toggle-on;
  border-bottom-color: transparent;
}

.nav-tabs>li>a:hover {
  border-color: $color-toggle-focus $color-toggle-focus $color-toggle-on;
}

.nav>li>a:focus,
.nav>li>a:hover {
  text-decoration: none;
  background-color: $color-toggle-focus;
  color: white;
}

.nav-tabs>li>a {
  margin-right: 2px;
  line-height: 1.42857143;
  border: 1px solid;
  border-color: $color-toggle-off $color-toggle-off $color-toggle-on;
  border-radius: 4px 4px 0 0;
  background-color: $color-toggle-off;
  color: #555;
}

.scale-image {
  display: block;
  width: 100%;
  height: auto;
}

.btn {
  padding: 6px 12px 3px;
}

.no-display {
  display: none !important;
}

textarea,
input[type="text"] {
  -webkit-appearance: none;
}



/* align glyph */
.left-addon .left-side {
  left: 0;
}

//.right-addon .right-side { right: 32px;}
.right-addon .right-side {
  right: 33px;
}

//.right-addon .absolute-right-side { right: 0;}
.right-addon .absolute-right-side {
  right: 2px;
}

.left-addon .ajax-loader-search {
  left: 0;
}

.right-addon .ajax-loader-search {
  right: 32px;
  top: 4px;
}

/* add padding  */
.left-addon input {
  padding-left: 45px;
}

.right-addon input {
  padding-right: 80px;
}

.table-borderless {

  th,
  td {
    border: none !important;
  }
}

.checkbox-below {
  margin-left: 5px;
  margin-top: 5px;

  span {
    font-size: 13px;
    line-height: 20px;
  }
}

.faq-items {
  margin: 15px 30px 15px 15px;

  a {
    color: #262626 !important;
    display: block;
  }

  dd {
    display: none;
    white-space: pre-line;
  }

  .expanded {
    dd {
      display: block;
    }
  }
}

.content {
  margin: 15px 30px 15px 15px;

  a {
    color: $color-important;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
}

.drawInfo {
  background-color: red;
  transition-timing-function: ease-in;
  transition: 0.4s;
  position: absolute;
  z-index: 100;
  margin: auto;
  left: 0;
  right: 0;
  text-align: right;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  opacity: 0.8;
  color: white;
}

.highPriorityBtn {
  z-index: 17;
  position: absolute;
}

.menuCloseBtn {
  font-size: 38px;
  cursor: pointer;
  font-weight: normal;
}

.noSearchHits {
  display: inline-grid;

  & span {
    padding: 4px 0;
  }

  & div {
    display: inline-grid;
    margin-top: 6px;
  }
}

